import React, { useState, useEffect } from "react";
import {
    OutlinedInput,
    Button,
    Box,
    Paper,
    Typography,
    List,
    ListItem,
    ListItemIcon,
    ListItemText,
} from "@material-ui/core";
import DirectionsBikeIcon from "@material-ui/icons/DirectionsBike";
import DriveEtaIcon from "@material-ui/icons/DriveEta";
import DirectionsWalkIcon from "@material-ui/icons/DirectionsWalk";
import AccessTimeIcon from "@material-ui/icons/AccessTime";
import DistanceIcon from "@material-ui/icons/PublicOutlined";
import LocationOnIcon from "@material-ui/icons/LocationOn";
import { makeStyles } from "@material-ui/core/styles";
import axios from "axios";

const API = "https://api.hcmus.fit";

const useStyles = makeStyles((theme) => ({
    transportIconContainer: {
        display: 'flex',
        gap: '16px',
        justifyContent: 'center',
        marginBottom: '16px',
    },
    iconButton: {
        cursor: 'pointer',
        padding: '12px',
        borderRadius: '12px',
        border: '2px solid #e0e0e0',
        transition: 'all 0.3s ease',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: '60px',
        height: '60px',
        '&:hover': {
            borderColor: '#0277BD',
            backgroundColor: 'rgba(2, 119, 189, 0.05)',
        },
        '&.selected': {
            borderColor: '#0277BD',
            backgroundColor: 'rgba(2, 119, 189, 0.15)',
        },
    },
    icon: {
        fontSize: '32px',
        color: '#0277BD',
    },
    searchDropdown: {
        marginTop: '8px',
        backgroundColor: '#fff',
        borderRadius: '8px',
        boxShadow: '0 4px 12px rgba(0, 0, 0, 0.15)',
        maxHeight: '300px',
        overflow: 'auto',
        position: 'absolute',
        width: '100%',
        zIndex: 1000,
    },
    listItem: {
        cursor: 'pointer',
        borderBottom: '1px solid #f0f0f0',
        padding: '12px 16px',
        '&:hover': { backgroundColor: 'rgba(2,119,189,0.05)' },
        '&:last-child': { borderBottom: 'none' },
    },
}));

const transportOptions = [
    { value: 'pedestrian', icon: DirectionsWalkIcon, label: 'i b' },
    { value: 'bicycle', icon: DirectionsBikeIcon, label: 'Xe p' },
    { value: 'car', icon: DriveEtaIcon, label: ' t' },
];

export default function SearchBoxRoutes(props) {
    const { setSelectPosition, onSearch, initialFrom = "", travelMode, setTravelMode } = props;
    const classes = useStyles();
    
    const [fromLocation, setFromLocation] = useState(initialFrom);
    const [toLocation, setToLocation] = useState("");
    const [toSearchResults, setToSearchResults] = useState([]);
    const [showToDropdown, setShowToDropdown] = useState(false);
    const [selectedTransport, setSelectedTransport] = useState(travelMode || "car");
    const [searchError, setSearchError] = useState('');
    const [isLocationSelected, setIsLocationSelected] = useState(false); // Track if location is selected

    // Update from location when initialFrom changes
    useEffect(() => {
        setFromLocation(initialFrom);
    }, [initialFrom]);

    // Search destination with debounce
    useEffect(() => {
        // Khng tm kim nu  chn a im
        if (isLocationSelected) {
            return;
        }

        if (!toLocation.trim()) {
            setToSearchResults([]);
            setShowToDropdown(false);
            setSearchError('');
            return;
        }

        const timer = setTimeout(async () => {
            try {
                const res = await axios.post(`${API}/search`, { 
                    address: toLocation 
                });
                setToSearchResults(res.data);
                setShowToDropdown(true);
                
                if (res.data.length === 0) {
                    setSearchError('Khng tm thy a im trong TP.HCM');
                } else {
                    setSearchError('');
                }
            } catch (err) {
                console.error("Search API Error:", err);
                setSearchError('Li khi tm kim a im');
            }
        }, 500);

        return () => clearTimeout(timer);
    }, [toLocation, isLocationSelected]);

    const handleToLocationSelect = (loc) => {
        const pos = loc.position;
        setSelectPosition({
            lat: pos.lat,
            lon: pos.lon,
            name: loc.address.freeformAddress
        });
        setToLocation(loc.address.freeformAddress);
        setShowToDropdown(false);
        setSearchError('');
        setToSearchResults([]); // Xa kt qu tm kim
        setIsLocationSelected(true); // nh du  chn a im
    };

    const handleTransportChange = (value) => {
        setSelectedTransport(value);
        if (setTravelMode) setTravelMode(value);
    };

    const handleSearch = () => {
        if (onSearch) {
            onSearch(selectedTransport, 'fastest');
        }
    };

    return (
        <Box data-route="item" style={{ display: "flex", flexDirection: "column", gap: "16px" }}>
            {/* From Location - Read Only */}
            <Box>
                <Typography variant="subtitle2" style={{ marginBottom: "8px", fontWeight: "bold" }}>
                    im i
                </Typography>
                <OutlinedInput
                    fullWidth
                    placeholder="Chn im bt u t bn "
                    value={fromLocation}
                    disabled
                    style={{ backgroundColor: "#f9f9f9" }}
                />
            </Box>

            {/* To Location - Searchable */}
            <Box style={{ position: 'relative' }}>
                <Typography variant="subtitle2" style={{ marginBottom: "8px", fontWeight: "bold" }}>
                    im ti
                </Typography>
                <OutlinedInput
                    fullWidth
                    placeholder="Nhp a ch ch trong TP.HCM"
                    value={toLocation}
                    onChange={(e) => {
                        setToLocation(e.target.value);
                        setIsLocationSelected(false); // Reset khi ngi dng typing
                    }}
                    onFocus={() => {
                        if (toLocation && !isLocationSelected) {
                            setShowToDropdown(true);
                        }
                    }}
                    onBlur={() => {
                        // ng dropdown sau 200ms  c thi gian click vo item
                        setTimeout(() => {
                            setShowToDropdown(false);
                        }, 200);
                    }}
                    style={{ backgroundColor: "#f9f9f9" }}
                />

                {/* Hin th li nu khng tm thy */}
                {searchError && toLocation && (
                    <Typography variant="caption" style={{ color: '#c62828', marginTop: 4, display: 'block' }}>
                        {searchError}
                    </Typography>
                )}

                {/* Dropdown kt qu tm kim */}
                {showToDropdown && toSearchResults.length > 0 && (
                    <Paper className={classes.searchDropdown}>
                        <List style={{ padding: 0 }}>
                            {toSearchResults.map((location, i) => (
                                <ListItem
                                    key={i}
                                    className={classes.listItem}
                                    onMouseDown={(e) => {
                                        e.preventDefault(); // Ngn onBlur trigger
                                        handleToLocationSelect(location);
                                    }}
                                >
                                    <ListItemIcon style={{ minWidth: 40 }}>
                                        <LocationOnIcon style={{ color: '#0277BD' }} />
                                    </ListItemIcon>
                                    <ListItemText
                                        primary={location.address.freeformAddress}
                                        secondary={location.poi?.name}
                                    />
                                </ListItem>
                            ))}
                        </List>
                    </Paper>
                )}
            </Box>

            {/* Transport Types */}
            <Box>
                <Typography variant="subtitle2" style={{ marginBottom: "12px", fontWeight: "bold" }}>
                    Phng tin di chuyn
                </Typography>
                <Box className={classes.transportIconContainer}>
                    {transportOptions.map((option) => {
                        const IconComponent = option.icon;
                        return (
                            <Box
                                key={option.value}
                                className={`${classes.iconButton} ${selectedTransport === option.value ? 'selected' : ''}`}
                                onClick={() => handleTransportChange(option.value)}
                                title={option.label}
                            >
                                <IconComponent className={classes.icon} />
                            </Box>
                        );
                    })}
                </Box>
            </Box>

            {/* Search Button */}
            <Button
                variant="contained"
                color="primary"
                size="large"
                fullWidth
                onClick={handleSearch}
                disabled={!fromLocation || !toLocation}
                style={{ padding: "12px", fontWeight: "bold" }}
            >
                Tm ng
            </Button>
        </Box>
    );
}