import React from 'react';
import { TileLayer, LayersControl, LayerGroup } from 'react-leaflet';
import './MapLayerControl.css';

const { BaseLayer } = LayersControl;

/**
 * MapLayerControl - Component  chuyn i gia cc loi bn 
 * H tr: OpenStreetMap, Satellite, Terrain, Dark Mode
 */
function MapLayerControl() {
    console.log('MapLayerControl rendered');

    return (
        <LayersControl position="topright">
            {/* OpenStreetMap - Map thng */}
            <BaseLayer checked name=" Bn  thng">
                <TileLayer
                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
            </BaseLayer>

            {/* Satellite - Map v tinh */}
            <BaseLayer name=" V tinh">
                <TileLayer
                    attribution='Tiles &copy; Esri &mdash; Source: Esri, i-cubed, USDA, USGS, AEX, GeoEye, Getmapping, Aerogrid, IGN, IGP, UPR-EGP, and the GIS User Community'
                    url="https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}"
                />
            </BaseLayer>

            {/* Satellite with Labels - V tinh c nhn */}
            <BaseLayer name=" V tinh + Nhn">
                <LayerGroup>
                    {/* Satellite */}
                    <TileLayer
                        attribution='Tiles &copy; Esri &mdash; Source: Esri, USDA, USGS, GeoEye...'
                        url="https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}"
                    />

                    {/* Labels (OSM road + place names) */}
                    <TileLayer
                        attribution='&copy; OpenStreetMap contributors'
                        url="https://tile.openstreetmap.org/{z}/{x}/{y}.png"
                        opacity={0.35}   //  trong sut  overlay
                    />
                </LayerGroup>
            </BaseLayer>

            {/* Terrain - Bn  a hnh */}
            <BaseLayer name=" a hnh">
                <TileLayer
                    attribution='&copy; <a href="https://opentopomap.org">OpenTopoMap</a> &mdash; Map data &copy; <a href="https://openstreetmap.org/copyright">OpenStreetMap</a>'
                    url="https://{s}.tile.opentopomap.org/{z}/{x}/{y}.png"
                />
            </BaseLayer>

            {/* Dark Mode - Bn  ti */}
            <BaseLayer name=" Ch  ti">
                <TileLayer
                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors &copy; <a href="https://carto.com/attributions">CARTO</a>'
                    url="https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}{r}.png"
                />
            </BaseLayer>

            {/* Light Mode - Bn  sng (minimal) */}
            <BaseLayer name=" Ch  sng">
                <TileLayer
                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors &copy; <a href="https://carto.com/attributions">CARTO</a>'
                    url="https://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}{r}.png"
                />
            </BaseLayer>
        </LayersControl>
    );
}

export default MapLayerControl;
